export const editDeviceBaseInfo = ({
  extMap,
  id,
  ratedPower,
  explosionProofGrade,
  ratedCurrent,
  serialNo,
  deviceName,
  deviceType,
  deviceModel,
  deviceCode,
  addrCode,
  warrantyDate,
  manufactureDate,
  pic,
  file,
  deviceId,
  principal,
  remark,
  assetId
}) => {
  return axios.request({
    url: 'device/editDevice',
    data: {
      assetId,
      extMap,
      deviceId,
      id,
      deviceName,
      serialNo,
      deviceType,
      ratedPower,
      explosionProofGrade,
      ratedCurrent,
      deviceModel,
      deviceCode,
      addrCode,
      warrantyDate,
      manufactureDate,
      pic,
      file,
      principal,
      remark
    },
    method: 'post'
  })
}

export const addDeviceBaseInfo = ({
  extMap,
  assetId,
  ratedPower,
  explosionProofGrade,
  principal,
  items,
  ratedCurrent,
  deviceId,
  serialNo,
  deviceName,
  deviceType,
  deviceModel,
  deviceCode,
  addrCode,
  warrantyDate,
  manufactureDate,
  pic,
  file,
  remark,
  deviceUser
}) => {
  return axios.request({
    url: 'device/addDevice',
    data: {
      deviceId,
      extMap,
      items,
      assetId,
      ratedPower,
      explosionProofGrade,
      principal,
      ratedCurrent,
      serialNo,
      deviceName,
      deviceType,
      deviceModel,
      deviceCode,
      addrCode,
      warrantyDate,
      manufactureDate,
      pic,
      file,
      remark,
      deviceUser
    },
    method: 'post'
  })
}

