import axios from '@/libs/api.request'

/**
 *  角色列表
 * @param pageNumber 分页码
 * @param pageSize 分页数
 * @returns {*}
 */
export const getRoleList = ({ pageNumber, pageSize, name, roleType }) => {
  const data = {
    pageNumber,
    pageSize,
    name,
    roleType
  }
  return axios.request({
    url: '/role/listRole',
    params: data,
    method: 'get'
  })
}

export const getRoles = ({ roleId }) => {
  const data = {
    roleId
  }
  return axios.request({
    url: '/userinfo/listByRoleId',
    params: data,
    method: 'get'
  })
}
/**
 * 获取角色详细信息
 * @param roleId 角色id
 * @returns {*}
 */
export const getRoleInfo = ({roleId }) => {
  const data = {}
  return axios.request({
    url: '/role/' + roleId,
    data,
    method: 'get'
  })
}

/**
 * 获取角色详细信息
 * @param orgId 区域id
 * @returns {*}
 */
export const getRoleAll = (params) => {
  return axios.request({
    url: '/role/listRolesAll',
    params,
    method: 'get'
  })
}

/**
 *
 * @param roleName 角色名称
 * @param remark 备注
 * @param roleType 角色类型
 * @param orgId 区域id
 * @param loginPlatform 登陆权限 1-web 2-微信 3-全部
 * @param modules 权限集合
 * @returns {*}
 */
export const addRole = ({ roleName, remark, roleType, modules, loginPlatform, orgId }) => {
  const data = {
    loginPlatform,
    orgId,
    roleName,
    roleType,
    remark,
    modules
  }
  return axios.request({
    url: '/role/addRole',
    data,
    method: 'post'
  })
}

/**
 * 编辑角色
 * @param roleId 角色id
 * @param orgId 区域id
 * @param loginPlatform 登陆平台
 * @param roleName 角色名称
 * @param roleType 角色类型
 * @param remark 备注
 * @param modules 权限集合
 * @returns {*}
 */
export const editRole = ({ orgId, loginPlatform, roleId, roleName, roleType, remark, modules }) => {
  const data = {
    orgId,
    loginPlatform,
    roleId,
    roleName,
    roleType,
    remark,
    modules
  }
  return axios.request({
    url: '/role/editRole',
    data,
    method: 'post'
  })
}

/**
 * 获取角色权限
 * @param roleId 角色id
 * @returns {*}
 */
export const getPermissionList = ({}) => {
  const data = {
  }
  return axios.request({
    url: '/module/listModule',
    params: data,
    method: 'get'
  })
}

/**
 *
 * @param roleId
 * @returns {*}
 */
export const deleteRole = ({ roleId }) => {
  const data = {
    roleId
  }
  return axios.request({
    url: '/role/delRole',
    data,
    method: 'post'
  })
}



