<template>
  <div>
    <Button size="large" type="text" @click="backHome">{{$t_p('backHome')}}</Button>
    <Button size="large" type="text">{{$t_p('backPre')}}({{ second }}s)</Button>
  </div>
</template>

<script>
  import './error.less'

  export default {
  name: 'backBtnGroup',
  i18n_prefix:'page.error-page.back-btn-group',
  data () {
    return {
      second: 5,
      timer: null
    }
  },
  methods: {
    backHome () {
      this.$router.replace({
        name: 'home'
      })
    },
    backPrev () {
      this.$router.go(-1)
    }
  },
  mounted () {
    this.timer = setInterval(() => {
      if (this.second === 0) this.backPrev()
      else this.second--
    }, 1000)
  },
  beforeDestroy () {
    clearInterval(this.timer)
  }
}
</script>



