<template>
  <div class="common-drawer">
    <Drawer v-model="modal"
            :width="width"
            :closable="false"
            :mask-closable="true">
      <div slot="header"
           class="now-form-title">
        <span>{{title}}</span>
      </div>
      <form-list v-bind="$attrs"
                 v-model="value"
                 @changeSelect ="changeSelect"
                 @selectTree="selectTree"
                 @getlocation="getlocation"
                 :selectLocation="selectLocation"
                 ref="form-list"></form-list>
      <div class="demo-drawer-footer">
        <Button style="margin-right: 8px"
                @click="modal=false">取消</Button>
        <Button type="default"
        class="confirmBtn"
                @click="confirm">提交</Button>
      </div>
    </Drawer>
  </div>
</template>

<script>
import formList from '@/components/common-form-list/common-form-list'

export default {
  inheritAttrs: false,
  name: 'common-drawer',
  components: {
    formList
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    width: {
      type: [String, Number],
      default: 22
    },
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    selectLocation: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      modal: false
    }
  },
  watch: {
    modal (val) {
      if (!val) {
        this.resetFields()
      }
    }
  },
  methods: {
    selectTree (e) {
      this.$emit('selectTree', e)
    },
    changeSelect (e) {
      this.$emit('changeSelect', e)
    },
    getlocation (e) {
      console.log('parent2', e)
      this.$emit('getlocation', e)
    },
    confirm () {
      this.$refs['form-list'].validate().then(() => {
        this.$emit('confirm', true)
        this.$refs['form-list'].confirm()
      }, () => {
        this.$emit('confirm', false)
      })
    },
    resetFields () {
      this.$refs['form-list'].resetFields()
    },
    open () {
      this.modal = true
    },
    close () {
      this.modal = false
    }
  }
}
</script>

<style lang="less" scoped>
.common-drawer {

}
.confirmBtn{
  color: #fff;
  background: #0084FF;
  :hover{
    opacity: 0.8;
  }
}
</style>



