<template>
  <TreeSelect
    class="org-select"
    :value="value"
    :placeholder="placeholder"
    @input="onSelectionChange"
    @select="onChange"
    noResultsText="没有数据"
    on-check-change="hasChecked"
    :multiple="false"
    :options="treeData"
  ></TreeSelect>
</template>

<script>
import {getAreaList} from '@/api/area'
import TreeSelect from '@riophae/vue-treeselect'

import '@riophae/vue-treeselect/dist/vue-treeselect.css'

import './org-select.less'

export default {
  name: 'all-org-select',
  i18n_prefix: 'component.all-area-select',
  data () {
    return {
      treeData: []
    }
  },
  props: {
    value: Number,
    flag: 0,
    placeholder: String,
    aa: Array
  },
  model: {
    prop: 'value',
    event: 'selection'
  },
  computed: {
    orgList () {
      return this.$store.state.org.list
    }
  },
  watch: {
    orgList () {
      this.refreshTree()
    },
    flag () {
      if (this.flag == 1) {
        this.loadTreeData()
      }
    }
  },
  methods: {
    loadTreeData () {
      getAreaList({
        level: 99
      })
        .then(res => {
          let batchdata1 = res.data
          let dataValueBatch1 = batchdata1 =>
            batchdata1.map(({ id, children, orgName }) => {
              let item = {
                id: id,
                label: orgName,
                children: dataValueBatch1(children)
              }
              if (item.children.length === 0) {
                delete item.children
              }
              return item
            })
          batchdata1.push({
            children: [],
            hasChild: 0,
            id: 1,
            level: 1,
            orgName: '所有区域',
            parentId: 1
          })
          this.treeData = dataValueBatch1(batchdata1)
        })
    },
    onChange (v) {
      this.$emit('selectTree', v.label)
    },
    onSelectionChange (v, i) {
      this.$emit('selection', v)
    }
  },
  mounted () {
    this.loadTreeData()
  },
  destroyed () {},
  components: {
    TreeSelect
  }
}
</script>

<style lang="less"></style>

<style lang="less" scoped>
@import "../../style/varient";

.org-select {
  color: #000;
  font-size: @size-font-normal;
  height: @size-font-lg * 2;
}
</style>



