<template>
  <Select class="status-select" :value="value" clearable transfer :placeholder="placeholder" @input="onSelectionChange">
    <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.label }}</Option>
  </Select>
</template>

<script>
// import {getDeviceItem} from '@/api/device'

export default {
  name: 'status-select',
  data () {
    return {
      options: [],
      optionMap: {}
    }
  },
  props: {
    value: String,
    placeholder: String,
    type: String
  },
  model: {
    prop: 'value',
    event: 'selection'
  },
  watch: {
    type () {
      this.loadTreeData()
    }
  },
  methods: {
    loadTreeData () {
      this.options = [
        {
          label: '运行',
          value: '1'
        },
        {
          label: '离线',
          value: '0'
        }
      ]
    },
    onSelectionChange (v) {
      this.$emit('selection', v)
      this.$emit('on-selection-change', this.optionMap[v])
    }
  },
  mounted () {
    this.loadTreeData()
  }
}
</script>

<style scoped lang="less">
.status-select{
  width: 100%;
}
</style>



