<template>
  <div class="form-list">
    <Form ref="form"
          :rules="formValidate"
          :model="value"
          :label-width="$flexible(110)"
          class="form">
      <div v-if="group">
        <div class="group"
             v-for="(item, index) in formList"
             :key="index">
          <div class="group-title">{{item.title}}</div>
          <form-list :formList="item.group"
                     v-model="value[item.value]"></form-list>
        </div>
      </div>
      <div v-else>
        <FormItem v-for="(item, index) in formList"
                  :key="index"
                  :label="item.label"
                  :prop="item.value"
                  :rules="item.rules">
          <div class="common-flex" v-if="item.type === 'input'">
            <Input :maxlength="100"
                  :placeholder="item.placeholder"
                  class="search-input"
                  v-model="value[item.value]"
                  :type="item.inputType || 'text'"
                  />
            <div v-if="item.unit" class="unit" style="font-size:8px">{{item.unit}}</div>
          </div>
          <DatePicker v-if="item.type === 'datePicker'" v-model="value[item.value]"
            format="yyyy/MM/dd HH:mm:ss"
            :type="item.range? 'datetimerange':'datetime'"
            :placeholder="item.placeholder"
            ></DatePicker>
            <DatePicker v-if="item.type === 'datePickerOnlyDate'" v-model="value[item.value]"
            format="yyyy-MM-dd"
            type="date"
            :placeholder="item.placeholder"
            ></DatePicker>
          <TimePicker v-if="item.type === 'timePicker'"
            type="timerange"
            v-model="value[item.value]"
            placement="bottom-end"
            :placeholder="item.placeholder"
          ></TimePicker>
          <Input v-if="item.type === 'textarea'"
                 type="textarea"
                 class="form-input"
                 :maxlength="100"
                 :autosize="{minRows: 2,maxRows: 5}"
                 :placeholder="item.placeholder"
                 v-model="value[item.value]" />
          <OrgSelect v-if="item.type === 'area-select'"
                     v-model="value[item.value]"
                     :placeholder="item.placeholder"
                     @selectTree="selectTree"
                     ref="tree"
                     v-bind="$attrs"></OrgSelect>
          <Select v-if="item.type === 'select'"
                  filterable
                  :disabled="item.disabled"
                  v-model="value[item.value]"
                  :placeholder="item.placeholder"
                  @on-change="changeSelect"
                  :clearable="item.clearable || false"
                  >
            <Option v-for="(option,index) in item.data" :key="index" :value="option.value">{{option.name}}</Option>
          </Select>
          <div class="common-flex" v-if="item.type === 'locationSelect'">
            <Input :maxlength="20"
                  :placeholder="item.placeholder"
                  class="search-input"
                  v-model="selectLocation"
                  :type="item.inputType || 'text'"
                  />
            <Button style="height:24px;margin-left:8px" @click="showMap" :disabled="slectStart">选择</Button>
          </div>
          <CheckboxGroup v-if="item.type === 'checkbox'"
                         v-model="value[item.value]">
            <Checkbox v-for="(checkItem, index) in item.data"
                      :key="index"
                      :label="checkItem.name"></Checkbox>
          </CheckboxGroup>
          <RadioGroup v-if="item.type === 'radio'"
                      v-model="value[item.value]">
            <Radio v-for="(checkItem, index) in item.data"
                   :key="index"
                   :label="checkItem.value">
                   {{checkItem.name}}
                   </Radio>
          </RadioGroup>
          <i-switch v-model="value[item.value] " v-if="item.type === 'switch'"  true-value="ON" false-value="OFF">
            <span slot="open">是</span>
            <span slot="close">否</span>
          </i-switch>
        </FormItem>
      </div>

    </Form>
    <div class="mapBox" id="map"></div>
  </div>
</template>

<script>
/**
 * 组件传入嵌套结构Form的整体检验不生效所以在每个配置项中添加独立的校验规则
 */
import OrgSelect from '@/components/all-area-select'

export default {
  name: 'form-list',
  components: {
    OrgSelect
  },
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    formList: {
      type: Array,
      default: () => {
        return []
      }
    },
    formValidate: {
      type: Object,
      default: () => {
        return {}
      }
    },
    group: {
      type: Boolean,
      default: false
    },
    selectLocation: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      map: {},
      slectStart: false,
      marker: {}
    }
  },
  watch: {
    selectLocation (val) {
      if (val === '') {
        this.initMap(2)
      }
    }
  },
  destroyed () {
  },
  methods: {
    showMap () {
      this.initMap()
      this.slectStart = true
    },
    initMap (type) {
      if (!type) {
        let beforeLng = this.selectLocation === '' ? 119.209927 : Number(this.selectLocation.split(',')[0])
        let beforeLat = this.selectLocation === '' ? 36.725914 : Number(this.selectLocation.split(',')[1])
        this.map = new AMap.Map('map', {
          zoom: 14, // 级别
          center: [beforeLng, beforeLat] // 中心点坐标
        })
        this.map.setMapStyle('amap://styles/2004f260216ebbad520cb0639fd2d51f')
        let self = this
        this.map.on('click', function (ev) {
        // 触发事件的地理坐标，AMap.LngLat 类型
          let lnglat = ev.lnglat
          self.selectLocation = lnglat.lng + ',' + lnglat.lat
          self.$emit('getlocation', lnglat)
          self.map.destroy()
          self.slectStart = false
        })
      } else {
        this.$emit('getlocation', '')
      }

      // this.genMarker(this.map, this.location)
    },
    genMarker (map, position) {
      let self = this
      self.marker = new AMap.Marker({
        position: position // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
      })
      map.add(self.marker)
    },
    changeSelect (e) {
      console.log(this.value)
      this.$emit('changeSelect', e)
    },
    selectTree (e) {
      this.$emit('selectTree', e)
    },
    confirm () {
      if (this.$refs.tree) {
        for (let item of this.$refs.tree) {
          item.loadTreeData()
        }
      }
    },
    resetFields () {
      this.$refs.form.resetFields()
    },
    validate () {
      return new Promise((resolve, reject) => {
        this.$refs.form.validate(res => {
          console.log(res)
          if (res) {
            resolve(res)
          } else {
            reject(res)
          }
        })
      })
    }
  }
}
</script>

<style lang="less" scoepd>
.form-list {
  .form {
    .ivu-form-item{
      .ivu-form-item-content{
        width: fit-content !important;
      }
    }
    .group {
      &-title {
        margin-bottom: 16px;
        font-size: 14px;
        font-family: Alibaba PuHuiTi;
        font-weight: 400;
        line-height: 32px;
        color: #fff;
      }
    }
    .ivu-form-item{
      margin-bottom: 0.33rem;
    }
    .ivu-select{
      width: 280px;
      .ivu-select-selection{
        background-color: rgba(0, 0, 0, 0.3);
      }
    }
  }
}
.ivu-time-picker-cells-cell:hover{
  color: #000;
}
.common-flex{
  display: flex;
  align-items: center;
}
.mapBox{
  position: fixed;
  right: 26%;
  top: 0;
  width: 62%;
  height: 100%;
  z-index: 1001;
  background: transparent !important;
}
.ivu-date-picker {
  input{
    width:355px !important
  }
}
</style>



