import axios from '@/libs/api.request'

/**
 *  用户区域列表
 * @param level 层级 ( 包含自己所在区域)
 * @returns {*}
 */
export const getAreaListAll = ({ level }) => {
  const data = {
    level
  };
  return axios.request({
    url: 'org/listOrganzationAll',
    params: data,
    method: 'get'
  })
};
/**
 *  用户区域列表
 * @param level 层级 ( 包含自己所在区域,顶级区域隐藏)
 * @returns {*}
 */
export const getAreaList = ({ level }) => {
  const data = {
    level
  };
  return axios.request({
    url: 'org/listOrganzationLevel',
    params: data,
    method: 'get'
  })
};


/**
 *  用户下述区域列表
 * @param level 层级
 * @returns {*}
 */
export const getUserAreaList = ({ level }) => {
  const data = {
    level
  };
  return axios.request({
    url: 'org/listOrganzation',
    params: data,
    method: 'get'
  })
};


/**
 *  用户下述区域列表
 * @param level 层级
 * @returns {*}
 */
export const getNextLevelAreaList = ({ parentId }) => {

  return axios.request({
    url: 'org/selectByParentId/'+parentId,
    params: {},
    method: 'get'
  })
};

/**
 *  根据父id查询下级区域信息
 * @param level 层级
 * @returns {*}
 */
export const getNextList = ({ id }) => {
  const data = id;
  return axios.request({
    url: `org/selectByParentId/${data}`,
    // params: data,
    method: 'get'
  })
};

/**
 * 新增区域
 * @param orgName 区域名
 * @param parentId 父id
 * @param alias 昵称
 * @returns {*}
 */
export const addArea = ({ orgName, parentId,alias}) => {
    const data = {
      orgName,
      parentId,
      alias
    };
    return axios.request({
      url: 'org/addOrg',
      data,
      method: 'post'
    })
  };

  /**
 * 编辑区域
 * @param id 区域id
 * @param orgName 区域名
 * @param parentId 父id
 * @param alias 昵称
 * @returns {*}
 */
export const EditArea = ({id, orgName, parentId,alias}) => {
  const data = {
    id,
    orgName,
    parentId,
    alias
  };
  return axios.request({
    url: 'org/editOrg',
    data,
    method: 'post'
  })
};

/**
 * 查询区域
 * @param orgId 区域id
 * @returns {*}
 */
export const serOrg = ({orgId }) => {
  const data = {};
  return axios.request({
    url: '/org/'+orgId,
    data,
    method: 'get'
  })
};

/**
 * 删除区域
 * @param id 角色id
 * @returns {*}
 */
export const delOrg = ({id }) => {
  const data = {id};
  return axios.request({
    url: '/org/delOrg',
    data,
    method: 'post'
  })
};

/**
 * 删除企业
 * @param id 角色id
 * @returns {*}
 */
export const delEnt = ({id}) => {
  const data = {id};
  return axios.request({
    url: '/org/delEnt',
    data,
    method: 'post'
  })
};

/**
 * 所有企业列表
 * @returns {*}
 */
export const listEnt = () => {
  return axios.request({
    url: 'org/listEnt',
    method: 'get'
  })
};

/**
 * 所有企业列表
 * @returns {*}
 */
export const listEntAll = ({
                             entName,
                             pageNumber,
                             pageSize
                           }) => {
  return axios.request({
    url: 'org/listEntAll',
    params: {
      entName,
      pageNumber,
      pageSize
    },
    method: 'get'
  })
};

/**
 * 新增企业
 * @param orgName 区域名
 * @param parentId 父id
 * @param alias 昵称
 * @returns {*}
 */
export const addEnt = ({orgName, alias}) => {
  const data = {
    orgName,
    alias
  };
  return axios.request({
    url: 'org/addEnt',
    data,
    method: 'post'
  })
};


/**
 * 编辑区域
 * @param id 区域id
 * @param orgName 区域名
 * @param parentId 父id
 * @param alias 昵称
 * @returns {*}
 */
export const editEnt = ({id, orgName, alias}) => {
  const data = {
    id,
    orgName,
    alias
  };
  return axios.request({
    url: 'org/editEnt',
    data,
    method: 'post'
  })
};

// 获取所有的自定义列表
export const listmenu = ({
                           entId
                           }) => {
  return axios.request({
    url: 'module/listEntView',
    params: {
      entId
    },
    method: 'get'
  })
};



