<template>
  <div class="chart-base" ref="chart_base"></div>
</template>

<script>

  import ec from 'echarts'

  export default {
    name: "chart-base",
    data() {
      return {
        //组件调用的时候，如果需要使用echarts实例方法可以使用ref来获取组件对象
        //具体方式如：this.$refs.xxxx.instance
        instance: null,
        isWaiting: false,
      }
    },
    props: {
      options: {
        type: Object,
        default() {
          return {}
        }
      },
      loading: Boolean,
      changeClear: {
        type:Boolean,
        default:true
      },
      delay: Number,
      active:{
        type:Boolean,
        default:true
      },
    },
    methods: {
      initChart() {
        window.$(window).on('resize', this.resizeChart)
        this.$eventHub.$on('sl-main-size-change', this.resizeChart)
        this.instance = ec.init(this.$refs.chart_base, {
          theme: 'macarons',
          ...this.options || {}
        });

        if (this.loading) {
          this.instance.showLoading()
        }


      },
      resizeChart() {
        // console.log("sl-main-size-change")
        if(!this.active){
          return
        }
        this.$nextTick(() => {
          setTimeout(() => {
            if (this.instance) {
              // console.log("sl-main-size-change resize")
              this.instance.resize();
            }
          }, 200)
        })
      },
      refreshChart() {

        // if(!this.active){
        //   return
        // }

        if (this.isWaiting) {
          return
        }
        if (this.instance) {
          if (this.changeClear) {
            this.instance.clear()
          }
          this.instance.setOption(this.options);
        }
        // console.log('refreshChart')
        // console.log(this.options)

      }
    },
    mounted() {
      this.initChart();
      this.isWaiting = true
      setTimeout(() => {
        this.isWaiting = false;
        this.refreshChart()
      }, this.delay)

    },
    watch: {
      active(v) {
        if (v) {
          this.resizeChart()
        }
      },
      loading(v) {
        if (v) {
          this.instance && this.instance.showLoading()
        } else {
          this.instance && this.instance.hideLoading()
        }
      },
      options() {
        this.refreshChart()
      }
    },
    destroyed() {
      window.$(window).off('resize', this.resizeChart)
      this.$eventHub.$off('sl-main-size-change', this.resizeChart)

      this.instance && this.instance.dispose()
    }
  }
</script>

<style scoped>
  .chart-base {
    width: 100%;
    height: 100%;

  }
</style>



