<template>
  <Select class="status-select" :value="value" clearable transfer :filterable="filterable" :placeholder="placeholder" @input="onSelectionChange">
    <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.label }}</Option>
  </Select>
</template>

<script>
import {getDeviceSearchList, getDeviceTypeList} from '@/api/device'
export default {
  name: 'status-select',
  data () {
    return {
      options: [],
      optionMap: {}
    }
  },
  props: {
    value: String,
    placeholder: String,
    type: Number,
    filterable: {
      type: Boolean,
      default: true
    }
  },
  model: {
    prop: 'value',
    event: 'selection'
  },
  methods: {
    loadTreeData () {
      if (this.type === 1) {
        this.getType()
      } else {
        this.getElse()
      }
    },
    getElse () {
      getDeviceSearchList().then(res => {
        let { code, data } = res
        if (code === 200) {
          switch (this.type) {
            case 2:
              this.options = data.map(i => {
                return {
                  label: i.deviceName,
                  value: i.deviceName
                }
              })
              break
            case 3:
              this.options = data.map(i => {
                return {
                  label: i.deviceCode,
                  value: i.deviceCode
                }
              })
              break
            case 4:
              this.options = data.map(i => {
                return {
                  label: i.deviceName,
                  value: i.id
                }
              })
              break
            default:
              this.options = [
                {
                  label: '运行',
                  value: 1
                },
                {
                  label: '关闭',
                  value: 0
                }
              ]
              break
          }
        }
      })
    },
    getType () {
      getDeviceTypeList().then(res => {
        let { code, data } = res
        if (code === 200) {
          this.options = data.map(i => {
            return {
              label: i.typeName,
              value: i.id
            }
          })
        }
      })
    },
    onSelectionChange (v) {
      this.$emit('selection', v)
      this.$emit('on-selection-change', this.optionMap[v])
    }
  },
  mounted () {
    this.loadTreeData()
  }
}
</script>

<style scoped lang="less">
.status-select{
  width: 100%;
}
</style>



