<template>
  <div class="com-tab">
    <div :class="['item', current === index?'active':'']"
         v-for="(item, index) in data"
         :key="item.id"
         @click="toggleTab(index)">
      <div class="inner">{{item.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'common-tab',
  props: {
    data: {
      type: Array,
      default: function () {
        return []
      },
    },
    current: {
      type: Number,
      default: 0,
    },
  },
  methods: {
    toggleTab(index){
      this.$emit('change', index)
    }
  },
}
</script>

<style lang="less" scoped>
.com-tab {
  .item {
    position: relative;
    display: inline-block;
    width: 144px;
    height: 40px;
    .inner {
      position: relative;
      width: 80%;
      height: 40px;
      margin: 0 auto;
      // background-color: rgba(255,255,255, .5);
      background-color: #212227;
      // color: rgba(0,0,0, .5);
      color: rgba(255,255,255, .6);
      border-radius: 10px 10px 0 0;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
      &::before{
        content: '';
        position: absolute;
        left: -10px;
        bottom: 0;
        width: 15px;
        height: 15px;
        // background: radial-gradient(30px at top left, transparent 50%, rgba(255,255,255, .5) 50%);
        background: radial-gradient(30px at top left, transparent 50%, rgba(33,34,39,1) 50%);
      }
      &::after{
        content: '';
        position: absolute;
        right: -10px;
        bottom: 0;
        width: 15px;
        height: 15px;
        // background: radial-gradient(30px at top right, transparent 50%, rgba(255,255,255, .5) 50%);
        background: radial-gradient(30px at top right, transparent 50%, rgba(33,34,39,1) 50%);
      }
    }
  }
  .active {
    .inner {
      // background-color: #fff;
      // color: #0084ff;
      background-color: #262C3A;
      color: #fff;
      &::after{
        content: '';
        // background: radial-gradient(30px at top right, transparent 50%, #fff 50%);
        background: radial-gradient(30px at top right, transparent 50%, #262C3A 50%)
      }
      &::before{
        content: '';
        // background: radial-gradient(30px at top left, transparent 50%, #fff 50%);
        background: radial-gradient(30px at top left, transparent 50%, #262C3A 50%);
      }
    }
  }
}
</style>


