import axios from '@/libs/api.request'

export const listDeviceItemByModel = ({modelId, pageSize, pageNumber}) => {
  return axios.request({
    url: '/deviceItem/listDeviceItemByModel',
    params: {
      modelId, pageSize, pageNumber
    },
    method: 'get'
  })
}

export const listDeviceItem = ({deviceId, pageSize, pageNumber}) => {
  return axios.request({
    url: '/deviceItem/listDeviceItem',
    params: {
      deviceId, pageSize, pageNumber
    },
    method: 'get'
  })
}

export const addDeviceItem = ({deviceId, modelId, itemName, alias, unit, dataType}) => {
  return axios.request({
    url: '/deviceItem/addDeviceItem',
    data: {
      deviceId, modelId, itemName, alias, unit, dataType
    },
    method: 'post'
  })
}

export const editDeviceItem = ({itemId, deviceId, modelId, itemName, alias, unit, dataType}) => {
  return axios.request({
    url: '/deviceItem/editDeviceItem',
    data: {
      itemId, deviceId, modelId, itemName, alias, unit, dataType
    },
    method: 'post'
  })
}

export const delDeviceItem = ({itemId}) => {
  return axios.request({
    url: '/deviceItem/delDeviceItem',
    data: {
      itemId
    },
    method: 'post'
  })
}

export const addModuleTemplate = ({
  name,
  xmlstring,
  refreshInterval
}) => {
  return axios.request({
    url: '/mxgraph/addMxgraphXmlTmp',
    data: {
      name,
      xmlstring,
      refreshInterval
    },
    method: 'post'
  })
}

export const listDeviceGroupData = ({pageNumber, pageSize, id}) => {
  return axios.request({
    url: '/deviceGroup/listDeviceGroupData',
    params: {
      pageNumber, pageSize, id
    },
    method: 'get'
  })
}

export const getModuleTemplateList = ({pageNumber, pageSize}) => {
  return axios.request({
    url: '/mxgraph/listMxgraphXmlTmp',
    params: {
      pageNumber, pageSize
    },
    method: 'get'
  })
}

export const getModuleList = ({pageNumber, pageSize}) => {
  return axios.request({
    url: '/mxgraph/listMxgraphXml',
    params: {
      pageNumber, pageSize
    },
    method: 'get'
  })
}

export const getDeviceList = ({name}) => {
  return axios.request({
    url: '/device/listDevice',
    params: {
      name
    },
    method: 'get'
  })
}

export const getDeviceItemList = ({deviceId}) => {
  return axios.request({
    url: '/device/listDeviceItem',
    params: {
      deviceId
    },
    method: 'get'
  })
}

export const getBindDataList = ({items}) => {
  return axios.request({
    url: '/mxgraph/bindData',
    data: items,
    method: 'post'
  })
}

export const getModuleInfo = ({id}) => {
  return axios.request({
    url: `/mxgraph/getMxgraphXml/${id}`,
    params: {},
    method: 'get'
  })
}

export const editModule = ({id, name, xmlstring, refreshInterval}) => {
  return axios.request({
    url: '/mxgraph/editMxgraphXml',
    data: {
      id, name, xmlstring, refreshInterval
    },
    method: 'post'
  })
}

export const getModuleResList = () => {
  return axios.request({
    url: '/mxgraph/listResource',
    method: 'get'
  })
}

export const addModuleImgRes = ({
  parentId,
  name,
  urlName
}) => {
  return axios.request({
    url: '/mxgraph/addResource',
    data: {
      parentId,
      name,
      urlName,
      type: 1,
      level: 2
    },
    method: 'post'
  })
}
export const deleteModuleImgRes = ({
  id
}) => {
  return axios.request({
    url: '/mxgraph/delResource',
    data: {
      id
    },
    method: 'post'
  })
}

export const moduleResUpload = () => {
  return axios.request({
    url: '/mxgraph/upload',
    method: 'get'
  })
}

export const editCmd = ({bindTime, devices, args}) => {
  return axios.request({
    url: '/device/issuedOrder',
    data: {
      bindTime, devices, args
    },
    method: 'post'
  })
}

export const getCmdList = ({pageSize, pageNo, deviceId}) => {
  return axios.request({
    url: '/device/listNoticeByType',
    params: {
      pageSize, pageNo, deviceId
    },
    method: 'get'
  })
}



