<template>
    <div class="laboratory-list">
      <Select class="maintain-item-select"   :value="value" :placeholder="placeholder" @input="onSelectionChange">
        <!--<Option value="" key="-1">请选择</Option>-->
        <Option v-for="item in options" :value="item.value" :key="item.value">{{ item.name }}</Option>
      </Select>
    </div>
</template>

<script>
    export default {
        name: "laboratory-list",
        components: {},
        data() {
            return {
              options: [],
              optionMap: {}
            }
        },
        computed: {},
        watch: {},
        methods: {
          loadTreeData(){
            this.options = [
              {
                name:'HiL系统',
                value:'1'
              },
              {
                name:'摇臂加载',
                value:'2'
              },
              {
                name:'浮动油封',
                value:'3'
              },
              {
                name:'整机倾斜',
                value:'4'
              },
              {
                name:'变频器加载',
                value:'5'
              },
              {
                name:'变频器产线',
                value:'6'
              }
            ]
          },
          onSelectionChange(v) {
            this.$emit('selection', v);
            this.$emit('on-selection-change', this.optionMap[v]);
          }
        },
        mounted() {
          this.loadTreeData()
        },
        destroyed() {
        }
    }
</script>

<style scoped>

</style>



