
import fault from '@/libs/api.fault'
import dpf from '@/libs/api.dpf'
import decision from '@/libs/api.decision'
import axios from 'axios'
export const getFaultList = (params) => {
  axios.get('http://vpowertest.weichai.com:30080/proxr/1000624/55968/784223e0dd5211ec925f3d628715cddc/8888/ft/list').then(res => {
    return res
  })
}

export const getFaultDetail = (params) => {
  axios.get('http://vpowertest.weichai.com:30080/proxr/1000624/55968/784223e0dd5211ec925f3d628715cddc/8888/ft/check?id=' + params.id).then(res => {
    return res
  })
}

export const getLifePredictList = (params) => {
  axios.get('http://vpowertest.weichai.com:30080/proxr/1000624/55932/5c979ad0e0b311eca7df399c5f6a9ca0/8888/life_predict?page=' + +params.pageNumber + '&page_size=' + params.pageSize + '&start_time=' + params.startTime + '&end_time=' + params.endTime + '&life_type=').then(res => {
    return res
  })
}

export const getDecisionList = (params) => {
  axios.get('http://vpowertest.weichai.com:30080/proxr/1000624/55930/d989b050e0b311ecafe82d423bee3d48/8888/decision?page=' + params.pageNumber + '&page_size=' + params.pageSize + '&start_time=' + params.startTime + '&end_time=' + params.endTime).then(res => {
    return res
  })
}



